import React, { useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import PROJECT_LIST from "constants/projectData";
import Contact from "component/contact";
import AOS from "aos";

import "swiper/css";
import styles from "./main.module.scss";

export default function Main() {
  useEffect(() => {
    /// AOS 
    AOS.init();
  }, []);

  const params = {
    spaceBetween: 10,
    slidesPerView: 1,
    centeredSlides: true,
    className: styles.mainSwiper,
    autoplay: {
      delay: 1000,
      disableOnInteraction: false,
    },
    breakpoints: {
      1024: {
        autoplay: true,
        slidesPerView: 2.25,
        spaceBetween: 8,
        centeredSlides: false,
      },
    },
  };

  return (
    <main>
      <section className={styles.mainPage}>
        <div className={styles.container}>
          {/*    */}
          <div className={styles.mainTitle}>
            <h2 data-aos="fade-up">' '</h2>
            <p data-aos="fade-up" data-aos-delay="100">
                   
              <br />
                     
              
            </p>
            <div
              data-aos="fade-up"
              data-aos-delay="200"
              className={styles.btnArea}
            >
              <a href="/project"> </a>
              <a href="/intro"></a>
            </div>
          </div>
        </div>
      </section>

      <section>
        {/*    */}
        <Swiper {...params} modules={[Autoplay]}>
          {PROJECT_LIST.map((item, index) => (
            <SwiperSlide key={index} className={styles.mainSlide}>
              <a href={item.readMore}>
                <img src={item.projectMainImage} alt="  " />
                <div className={styles.projectInfo}>
                  <h3>{item.projectName}</h3>
                  <p>{item.projectContent} </p>
                  {item.projectType.map((type, index) => {
                    return <span key={index}>{type}</span>;
                  })}
                </div>
              </a>
            </SwiperSlide>
          ))}
        </Swiper>
      </section>

      {/*   */}
      <Contact />
    </main>
  );
}
